.header-strip {
  background-color: white !important;
  box-shadow: none !important;
  flex-direction: row !important;
  width: 100%;
  height: 73px;
  padding-right: 32px;
  padding-left: 32px;
  box-sizing: border-box;
  border-bottom: 1px solid #ebe7f0;

  .tool-bar {
    width: 100%;
    justify-content: space-between;
  }
  .logo {
    width: 140px;
    vertical-align: middle;
    height: 100%;
    cursor: pointer;
  }
  .log-container {
    width: 140px;
    height: 100%;
    border-bottom: 2px solid #5855e4;

    svg {
      margin-top: 10px;
      width: 120px;
      height: 50px;
    }
  }
  .right-section {
    width: unset;
  }
  .help-btn {
    font-size: 14px;
    min-width: unset;
    color: #4a4a4a;
    cursor: pointer;
    span {
      vertical-align: middle;
    }
    svg {
      font-size: 14px;
      vertical-align: middle;
    }
    &:hover {
      color: #19cfa6;
    }
  }
}
